.techs {
	background-color: #272727;
}

.techs__container {
	background-color: #272727;
	max-width: 1280px;
	margin: 0 auto;
}

.techs__header {
	background-color: #272727;
	margin: 0 auto;
	padding: 100px 0 23px 0;
	width: 1140px;
	border-bottom: 1px #dadada solid;
	font-size: 22px;
	font-weight: 400;
	line-height: 1.21;
	letter-spacing: -0.9px;
}

.techs__title {
	margin: 89px auto 25px;
	font-size: 50px;
	font-weight: 400;
	line-height: 1.21;
	text-align: center;
	letter-spacing: -1.5px;
}

.techs__subtitle {
	margin: 0 auto 100px;
	width: 470px;
	font-size: 14px;
	font-weight: 400;
	line-height: 1.42;
	text-align: center;
	letter-spacing: -0.4px;
}

.techs__list {
	padding: 0;
	display: flex;
	gap: 10px;
	justify-content: center;
	list-style: none;
	margin: 0 70px;
}

.techs__skill {
	display: flex;
	margin: 0 auto 101px;
	background-color: #303030;
	width: 90px;
	height: 60px;
	border-radius: 10px;
	align-items: center;
	justify-content: center;
	font-size: 12px;
}

.techs__skill:hover {
	background-color: #3ddc84;
	color: #303030;
	cursor: default;
}

@media screen and (max-width: 1279px) {
	.techs__container {
		max-width: 768px;
	}

	.techs__header {
		padding: 91px 0 23px;
		max-width: 668px;
	}

	.techs__title {
		margin: 80px auto 22px;
		line-height: 1.16;
		letter-spacing: -1.8px;
	}

	.techs__subtitle {
		margin: 0 auto 83px;
		width: 460px;
		font-size: 12px;
		line-height: 1.5;
	}

	.techs__list {
		margin: 0 60px;
	}

	.techs__skill {
		margin: 0 auto 90px;
		font-size: 12px;
		width: 84px;
		height: 57px;
	}
}

@media screen and (max-width: 767px) {
	.techs__container {
		max-width: 460px;
	}

	.techs__header {
		width: 460px;
	}

	.techs__list {
		flex-wrap: wrap;
		margin: 0 71px;
		padding-bottom: 70px;
	}

	.techs__skill {
		margin: 0 auto;
		background-color: #303030;
		width: 84px;
		height: 57px;
	}
}

@media screen and (max-width: 500px) {
	.techs__container {
		max-width: 320px;
	}

	.techs__header {
		padding: 69px 0 28px 0;
		width: 284px;
		font-size: 18px;
		letter-spacing: -0.8px;
	}

	.techs__title {
		margin: 60px auto 24px;
		font-size: 30px;
		letter-spacing: -1.2px;
	}

	.techs__subtitle {
		margin: 0 auto 50px;
		width: 292px;
		font-size: 11px;
	}

	.techs__list {
		justify-content: flex-start;
	}
}
